@use 'base/variables' as v;
 
.alerta {
    padding: 1rem;
    color: v.$blanco;
    width: 100%;
    display: block;
    margin-bottom: 2rem;
    text-align: center;
    font-weight: v.$bold;
    text-transform: uppercase;

    &.error {
        background: rgb(172,13,84);
        background: -moz-linear-gradient(90deg, rgba(172,13,84,1) 57%, rgba(193,1,13,0.6741071428571428) 80%);
        background: -webkit-linear-gradient(90deg, rgba(172,13,84,1) 57%, rgba(193,1,13,0.6741071428571428) 80%);
        background: linear-gradient(90deg, rgba(172,13,84,1) 57%, rgba(193,1,13,0.6741071428571428) 80%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ac0d54",endColorstr="#c1010d",GradientType=1);
    }

    &.exito {
        background: rgb(135,176,55);
        background: -moz-linear-gradient(90deg, rgba(135,176,55,0.8701855742296919) 56%, rgba(22,98,17,0.6741071428571428) 80%);
        background: -webkit-linear-gradient(90deg, rgba(135,176,55,0.8701855742296919) 56%, rgba(22,98,17,0.6741071428571428) 80%);
        background: linear-gradient(90deg, rgba(135,176,55,0.8701855742296919) 56%, rgba(22,98,17,0.6741071428571428) 80%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#87b037",endColorstr="#166211",GradientType=1);
    }
}
