@use 'base/variables' as v;
@use 'base/mixins' as m;


.listado-servicios {
    display: grid;
    gap: 2rem;
    @include m.tablet {
        display: grid;
        @include m.grid(2, 2rem)
    }

    .servicio {
        background-color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 200ms;
        text-align: center;

        &.seleccionado {
            background-color: v.$azul;

            p {
                color: v.$blanco;
            }
        }

        &:hover {
            transform: scale(1.02);
            cursor: pointer;
        }

        p {
            color: v.$negro;
            margin: 0 0 1.3rem 0;
        }

        .precio-servicio {
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 2.5rem;
            margin: 0;
        }

    }
}