@use 'base/variables' as v;
@use 'base/mixins' as m;

.citas {
    list-style: none;
    padding: 0;

      li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;
            p{
                font-weight: v.$bold;
                color: v.$azul;
            }

            span, .servicio, .total {
                font-weight: v.$regular;
                color: v.$blanco;
            }

            h3 {
                margin: 5rem 0 0 0;
            }
      }
}


.servicios {
    @extend .citas;

    .acciones {
        margin: -2rem 0 -2rem 0;
        .boton,
        .boton-elimnar {
            margin: 0;
        }
    }
}