@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url('../img/1.jpg');
        background-size: cover;
        background-position: center center;
        @include m.tablet {
            height: auto;
        }
    }

    .app {
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet {
            overflow: scroll;
        }
        
    }
}


.barra {
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        .boton {
            margin-bottom: 6rem;
            margin-top: 1rem;
        }
    }

    p{
        margin-top: 6rem;
    }

    .boton {
        @include m.boton(v.$negro, v.$blanco);
        border-radius: 2.5rem;
        padding: 1rem 2rem;
        
    }
}

.barra-admin {
    @include m.tablet {
        display: flex;
        justify-content:space-between;
        margin-bottom: 5.5rem;
    }

    .boton {
        margin: 0 0 1rem 0;
        padding: 1rem 2rem;
        @include m.desktop {
            border-radius: 2rem;
        }
    }
}